@import url(https://fonts.googleapis.com/css2?family=Montagu+Slab:wght@200;300;400;600&display=swap);
*{
    font-family:'Montagu Slab', serif;
}

*{
    box-sizing: border-box;
}

.main_container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

}
.preview_section{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    width: 75%;
    margin-top: 30px;
}
.form_section{
    width: 40%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #f3efe7;
    padding: 20px;
}
input {
    height: 45px; 
}
input, textarea {
    width:100%;
    margin-bottom: 1em;
    font-size: .9em;
    font-weight: 300;
    padding: .5em 1em;
    border: none;
    color: #302f2f;
    font-weight: 300;
}
button{
    cursor: pointer;
    margin: 0;
    padding: .5em 2em;
    background: #636363;
    border: none;
    color: #fff;
    font-size: 1em;
    font-weight: 400;
    width:100%;
    margin-top: 20px;
}
*{
    box-sizing: border-box;
}
.header_container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 2px solid gray;
    width: 40%;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 20px;
}
.name{
    font-size: 3em;
}

.title{
    margin-top: -20px;
    font-weight: 300;
}
.body_container{
    margin: 20px auto;
    display:grid;
    grid-template-columns: minmax(auto, auto) 1fr;
    word-wrap: break-word
}
.education_container{
    width: 85%;
    padding-left: 100px;
    margin-top: 100px;
}
.about_container{
    width: 85%;
    margin-left: 20px;
    margin-top: 100px;
}
.skills_container{
    width: 85%;
    padding-left: 100px;
    margin-top: 100px;
}
.work_container{
    width: 70%;
    padding-left: 100px;
    margin-top: 100px;
}
.contact_container{
    width: 500px;
    padding-left: 100px;
    margin-top: 100px;
}
.edu_title, .skills_title, .about_title,  .contact_title{
    font-size: 2em;
    font-weight: 400;
    border: 2px solid gray;
    width: 60%;
}
.expi_title{
    width: 75%;
    font-size: 2em;
    font-weight: 400;
    border: 2px solid gray;
}
h5{
    font-weight: 400;
}


